<template>
  <v-container fluid fill-height>
    <v-layout row wrap align-center justify-center>
      <v-flex xs12 hidden-sm-and-up style="margin-top:90%;">blah</v-flex>
      <v-flex xs12 sm6>
        <v-card class="elevation-18 rounded-card">
          <v-layout row wrap fill-height>
            <v-flex hidden-sm-and-down class="round-left">
              <v-card
                class="round-left"
                text-md-center
                height="100%"
                flat
                color="#7DA5C6"
              >
                <v-container fluid fill-height>
                  <v-layout align-center justify-center>
                    <v-flex>
                      <v-img
                        height="8em"
                        contain
                        :src="`${publicPath}/img/logos/folk.ico`"
                      ></v-img>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
            <v-flex class="round-right">
              <v-card flat class="round-right">
                <v-card-title class="justify-center">
                  <h1 style="color:#2E7395;">Members Login</h1>
                </v-card-title>
                <v-layout pa-4 column>
                  <v-flex xs12>
                    <v-form v-model="valid" ref="form">
                      <v-text-field
                        v-model="name"
                        prepend-inner-icon="person"
                        :rules="nameRules"
                        :counter="10"
                        validate-on-blur
                        label="Name"
                        required
                      ></v-text-field>
                      <v-text-field
                        v-model="password"
                        prepend-inner-icon="lock"
                        :append-icon="show ? 'visibility' : 'visibility_off'"
                        :rules="passwordRules"
                        :type="show ? 'text' : 'password'"
                        validate-on-blur
                        name="input-10-1"
                        label="Password"
                        hint="At least 8 characters"
                        counter
                        @click:append="show = !show"
                      ></v-text-field>

                      <v-checkbox
                        v-model="checkbox"
                        label="Remember me?"
                        required
                      ></v-checkbox>
                      <v-layout row>
                        <v-flex xs3>
                          <v-btn color="#255F7C" small dark @click="submit"
                            >Login</v-btn
                          >
                        </v-flex>
                      </v-layout>
                    </v-form>
                  </v-flex>
                </v-layout>
                <v-card-actions>
                  <span style="color:#255F7C;">Don't have an account?</span>
                  <v-btn small flat type="link" to="/signup">
                    <u style="color:#0061FF;">Register here</u>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      publicPath: process.env.BASE_URL,
      valid: false,
      show: false,
      password: "",
      passwordRules: [
        v => !!v || "Password is required",
        v => (v && v.length >= 8) || "Min 8 characters",
        () =>
          this.emailMatch ||
          "The Username and password you entered were not found!"
      ],
      name: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 10) || "Name must be less than 10 characters"
      ],
      checkbox: "",
      emailMatch: true
    };
  },
  methods: {
    submit() {
      this.emailMatch = true;
      if (this.$refs.form.validate()) {
        this.getUser();
      }
    },
    getUser() {
      // this will find the user from the localstorage or prefilled JSON
      this.$store
        .dispatch("fetchUser", { name: this.name, password: this.password })
        .then(res => {
          console.log("sign in", res);
          this.$router.push({ path: "/landing" });
        })
        .catch(err => {
          console.log(err);
          this.emailMatch = false;
          this.$refs.form.validate();
        });
    }
  }
};
</script>
<style scoped lang="scss">
.rounded-card {
  border-radius: 50px;
}
.round-left {
  border-radius: 50px 0px 0px 50px;
}
.round-right {
  border-radius: 0px 50px 50px 0px;
}
</style>
